
:root{
    --header-height: 4rem;
    --color-header: #2d1545;
    /* --color-header: #4f019d; */
    --color-background: #1a141f;
    --color-text: white;
    --color-text-inverse: #1a141f;
    --color-work-header: #7b44b4;
    --color-card: #7b44b4;
    --color-sub-titile: #7b44b4;
    /* --color-work-content: #3c2b4c; */
    --color-work-content: #352a3f;
    --color-project-card: #5e4377;
    --color-link: #bda7d1;
    --color-link-inverse: #3c2b4c;

    --color-box-border: #7b44b4;

    --color-input: #20152b;
}

/* Apply box-sizing to all elements for better layout control */
*,
*::before,
*::after {
    box-sizing: border-box;
}

html, body {
    /* height: 100%; */
    height: 100vh;
    margin: 0;
    padding: 0;
    /* overflow-x: hidden; */
    max-width: 100%
}

/* #root, .App{
    height: 100vh;
} */
/* * {
    outline: 1px solid red;
  } */

body {
    max-width: 100%;
    overflow-x: hidden;
    text-align: center;
    margin: 0px;
    padding: 0px;
    /* FUENTES */
    /* font-family: "Montserrat Thin"; */
    font-family: 'Segoe UI', Roboto, Helvetica Neue;
    -webkit-font-smoothing: antialiased;
    -moz-font-smoothing: antialiased;
    -moz-osx-font-smoothing: antialiased;
    background-color: var(--color-background);
    color: var(--color-text);
}

.page-container {
    display: flex;
    flex-direction: column;
    /* height: 100%; */
    min-height: calc(100vh);
    max-width: 100%;
    /* height: 100vh; */
    /* overflow: auto; */
    overflow-x: hidden;
    /* position: relative; */
}

.body-content {
    flex: 1
}

.external-link{
    /* color: inherit; */
    color: var(--color-link);
}

.external-link.inverse{
    color: var(--color-link-inverse);
}



/* ###################### */
/* UTILS*/
/* ###################### */
/* #region utils  */
.cursor-pointer {
    cursor: pointer;
}
/* #endregion */

.header{
    height: var(--header-height);
    background-color: var(--color-header);
    color: var(--color-text);
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
    /* justify-content: space-between; */
    position: fixed;
    top: 0;
    left: 0;
    z-index: 50;
    width: 100%;
}

.footer{
    height: 4rem;
    background-color: #2d1545;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    font-size: 0.8rem;
}

.arrow-to-top {
    position: fixed;
    bottom: 3rem;
    right: 0;
    z-index: 50;
}

.welcome-page{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: center; */
}

/* ###################### */
/* NAV BAR*/
/* ###################### */
/* #region navbar  */

.nav-bar {
    display: flex;
    flex-direction: row;
    gap: 1.2rem;
    padding-right: 2rem;
}

.nav-bar-item{
    cursor: pointer;
    font-size: 1.1rem;
}

/* #endregion */

/* ###################### */
/* SECTIONS*/
/* ###################### */
/* #region sections  */
.section {
    padding-top: var(--header-height);
    /* width: 1300px; */
    width: 70rem;
    max-width: 100%;
    min-height: calc(100vh - var(--header-height));
    /* border: 1px solid blue; */

    /* display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center; */
}

.section-header {
    width: 100%;
    /* border: 1px solid red; */
    margin-bottom: 4rem;
    font-size: 2.5rem;
}

/* .section-header2::after {
    content: "";
    position: absolute;
    left: 50%;
    bottom: 0;
    width: 0;
    height: 10px;
    background-color: #ff4d58;
    z-index: -1;
    transition: width 0.3s ease-in-out, left 0.3s ease-in-out;
} */
.section-header::after {
    position: absolute;
    content: '';
    border-bottom: 1.1rem solid var(--color-sub-titile);
    width: 15rem;
    display: block;
    margin: 0 auto;
    position: relative;
    left: 4rem;
    top: -0.8rem;
    z-index: -1;
}

.section-content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
/* #endregion */

/* ###################### */
/* HOME*/
/* ###################### */
/* #region home */
.description{
    margin-bottom: 2rem;
    width: 60%;
}
.custom-button{
    border: 2px var(--color-sub-titile) solid;
    background-color: rgba(0,0,0,0);
    color: var(--color-text);
    border-radius: 5px;
    padding: 0.7rem 1.5rem 0.7rem 1.5rem;
    cursor: pointer;
}

.custom-button.icon{
    padding-right: 1.1rem;
}

.custom-button.disabled{

}

.custom-button-spinner-container{
    height: 1rem !important;
    font-size: 1rem;
}



.custom-button-spinner{
    color: white !important;
    border-width: 0.1rem !important;
    height: 0.9rem !important;
    width: 0.9rem !important;
}

.button-link{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
}

.section-content-main{
    height: calc(100vh - var(--header-height)) ;
    /* border: 1px solid green; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.section-content-main h1{
    font-size: 3rem;
}

.type-effet-cursor{
    color: var(--color-sub-titile);
    /* font-size: 2rem; */
    /* padding-bottom: 1rem; */
}
.title {
    position: relative;
    display: inline-block; /* Ensures text doesn't stretch */
    
    /* Reserve space for the underline so text doesn't move */
    /* padding-bottom: 1.2rem; */
}

.title::after {
    content: ''; 
    position: absolute;
    width: 30rem; /* Set final width */
    height: 1.1rem;
    left: 15rem;
    top: 2.8rem;
    z-index: -1;
    background: var(--color-sub-titile);
    
    /* Keeps Space Reserved */
    transform: scaleX(0);
    transform-origin: left;
    opacity: 0; /* Keeps it hidden */
    transition: transform 2s ease-out, opacity 0.5s ease-out;
}

/* When Typing is Completed */
.title.completed::after {
    transform: scaleX(1);
    opacity: 1;
}


/* Keyframes to Reveal Without Moving Text */
@keyframes revealLine {
    0% { transform: scaleX(0); opacity: 1;}
    100% { transform: scaleX(1); opacity: 1;}
}
/* #endregion */

