    flex-direction: column;
    align-items: center;
    gap: 1rem;
}

.work-card {
    width: 56rem;
}

.work-card-header{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    background-color: var(--color-work-header);
    height: 3rem;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    font-weight: bold;
}


.work-card-mobile{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    gap:0.2rem;
    flex-grow: 1;
    /* width:1rem */
}


.work-card-title{
    margin-left: 1.5rem;
}
.work-card-subtitle{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-grow: 1;
    width: 100%;
    margin-left: 1.5rem;
    padding-right: 1.5rem;
    font-weight: 500;
    font-size: 0.9rem;
}

.work-card-dates{
    display: flex;
    flex-direction: row-reverse;
    gap: 0.5rem;
    margin-right: 1.5rem;
}

.work-card-dates.mobile{
    font-weight: 500;
    font-size: 0.9rem;
    /* margin-left: 1.5rem; */
}

.work-card-mobile-icon{
    margin-right: 1rem;
}

.work-card-wrap{
    max-height: 0;
    transition: cubic-bezier(.4,0,.2,1) .4s;
    overflow: hidden;
}

.work-card-wrap.visible{
    max-height: 100rem;
}

.work-wrap-content{
    background-color: var(--color-work-content);
    margin-top: 1rem;
    border-radius: 5px;
    display: flex;
}

.work-wrap-content-data{
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    margin-top: 1rem;
    margin-left: 1.5rem;
    margin-bottom: 1.5rem;
}

.work-wrap-location{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 0.5rem;
}

.work-wrap-summary{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    text-align: left;
    gap: 0.7rem;
    width: 50rem;
    /* margin-left: 0 !important;
