    flex-direction: row;
    flex-wrap: wrap;
    gap: 0.8rem;
}

.work-wrap-skill{
    background-color: beige;
    border-radius: 15px;
    padding: 0.3rem 0.8rem;
    color: var(--color-text-inverse);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: 0.9rem;
}

