/* #region contact  */
.contact-content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.contact-social-container{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height: 2rem;
    gap: 1rem;
    /* margin-bottom: 2rem; */
    margin-top: 2rem;
}
.contact-social-logo{
    height: 100%;
}

.social-logo-container{
    height: 2rem;
}

.contact-text{
    width: 50%;
}
.contact-form-container{
    background-color: #3c2b4c;
    margin-top: 2rem;
    width: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 7px 7px 0 0;
}

.contact-form {
    display: flex;
    flex-direction: column;
    gap: .7rem;
    align-items: center;
    width: 80%;
    margin-top: 3rem;
    margin-bottom: 3rem;
}

.form-group {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: .3rem;
    width: 100%;
}

input {
    border-radius: 3px;
    width: 100%;
    height: 2.3rem;
    padding-left: 0.5rem;
    font-family: inherit;
    font-weight: 500;
    background-color: var(--color-input);
    border: 0;
    color: #c8bed1;

}

textarea {
    border-radius: 3px;
    resize: none;
    width: 100%;
    max-width: 100%;
    height: 8rem;
    min-height: 8rem;
    padding: 0.5rem;
    font-family: inherit; /* Ensures it takes font from body */
    /* font-size: 0.85rem; */
    font-weight: 500;

    background-color: var(--color-input);
    border: 0;
    color: #c8bed1;
}

.custom-button.submit{
    margin-top: 1rem;
    min-width: 6rem;
    border: 0;
    background-color: #502e6e;
    /* background-color: var(--color-input); */
}

.custom-button.submit:hover{
    background-color: #7b44b4;
}


